import React from "react";
import { MDXProvider } from "@mdx-js/react"; // reference: https://www.gatsbyjs.com/docs/how-to/routing/customizing-components/
import { ThemeProvider } from "styled-components";
import { GlobalStyle } from "../styles/global-style";
import { darkTheme, lightTheme } from "../styles/themes";
import { LIGHT } from "../constants";
import { useThemeToggle } from "../hooks";
import Layout from "./layout";
import { shortcodes } from "../styles/markdown-style";

const App = ({ children }) => {
  const [themeMode, toggleTheme, componentMounted] = useThemeToggle();

  if (!componentMounted) {
    return <></>;
  }

  return (
    <>
      <ThemeProvider theme={themeMode === LIGHT ? lightTheme : darkTheme}>
        <MDXProvider components={shortcodes}>
          <GlobalStyle />
          <Layout toggleTheme={toggleTheme}>{children}</Layout>
        </MDXProvider>
      </ThemeProvider>
    </>
  );
};

export default App;
